html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  font-family: sans-serif;
  font-size: 16px;
  margin: 8px;
  padding: 0;
}

h1 {
  border-bottom: 1px solid;
  font-size: 16px;
}

h2 {
  border-bottom: 1px solid;
  font-size: 14px;
}

pre {
  font-size: 14px;
  white-space: pre-wrap;
}



